import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WayfinderComponent } from './wayfinder.component';

const routes: Routes = [
    {
        path: '', component: WayfinderComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class WayfinderRoutingModule { }
