import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { TemplatePortal } from '@angular/cdk/portal';
import { Component, EventEmitter, OnInit, Output, TemplateRef, ViewChild, ViewContainerRef } from '@angular/core';
import { MatButton } from '@angular/material/button';
import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-path-direction',
  templateUrl: './path-direction.component.html',
  styleUrls: ['./path-direction.component.scss']
})
export class PathDirectionComponent implements OnInit {


  @Output() navDirection = new EventEmitter();
  fromPoint: any;
  toPoint: any;

  // Private
  private _selectPanelOverlayRef: OverlayRef;

  @ViewChild('glAccountPanel')
  private _glAccountPanel: TemplateRef<any>;

  @ViewChild('addAccountElem')
  private _addAccountElem: MatButton;

  constructor(private _overlay: Overlay,
    private sharedService : SharedService,
    private _viewContainerRef: ViewContainerRef) { }

  ngOnInit(): void {
  }

  openGlAccount() {

    // Create the overlay
    this._selectPanelOverlayRef = this._overlay.create({
      backdropClass: '',
      hasBackdrop: true,
      scrollStrategy: this._overlay.scrollStrategies.block(),
      positionStrategy: this._overlay.position()
        .flexibleConnectedTo(this._addAccountElem._elementRef.nativeElement)
        .withFlexibleDimensions()
        .withViewportMargin(56)
        .withLockedPosition(false)
        .withPositions([
          {
            originX: 'start',
            originY: 'bottom',
            overlayX: 'start',
            overlayY: 'top'
          }
        ])
    });

    // Create a portal from the template
    const templatePortal = new TemplatePortal(this._glAccountPanel, this._viewContainerRef);

    // Attach the portal to the overlay
    this._selectPanelOverlayRef.attach(templatePortal);

    // Subscribe to the backdrop click
    this._selectPanelOverlayRef.backdropClick().subscribe(() => {

      // If overlay exists and attached...
      if (this._selectPanelOverlayRef && this._selectPanelOverlayRef.hasAttached()) {
        // Detach it
        this._selectPanelOverlayRef.detach();

      }

      // If template portal exists and attached...
      if (templatePortal && templatePortal.isAttached) {
        // Detach it
        templatePortal.detach();
      }
    });

  }

  submitDirections(form) {
    let navObj = {
      from: this.fromPoint,
      to: this.toPoint,
    }
    this._selectPanelOverlayRef.detach();
    this.navDirection.emit(navObj);
  }

}
